@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
@import url("https://fonts.googleapis.com/css?family=Oswald&display=swap");

html {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0;
  font-family: Roboto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(
    138deg,
    rgb(26, 30, 36) 20%,
    rgb(40, 44, 52) 50%,
    rgb(26, 30, 36) 80%
  );
}

#root {
  flex: 1 0 auto;
}

.recharts-responsive-container {
  margin-top: 3em;
  margin-bottom: 5em;
  margin: 3em auto;
}
.recharts-default-tooltip {
  box-shadow: 0.1em 0.1em 1em 0em rgba(0, 0, 0, 0.5);
  transform: perspective(300px) rotateY(-2deg);
}

.recharts-tooltip-item {
  margin-right: 1em;
}

.recharts-surface {
  margin-left: -10%;
  transform: perspective(300px) rotateY(-2deg);
}
.recharts-legend-wrapper {
  transform: perspective(0px) rotateY(0deg);
}
.recharts-legend-item-text,
.recharts-tooltip-item-name {
  font-family: Oswald;
  text-transform: capitalize;
}

a {
  color: azure;
  font-family: Oswald;
}

h1 {
  color: azure;
  font-family: Oswald;
  padding: 0 1em;
  margin: 1em auto;
}
p {
  line-height: 1.4em;
  margin: 1em auto;
  max-width: 720px;
  color: azure;
  padding: 0em 1em;
  text-align: left;
}
footer {
  background-color: rgb(26, 30, 36);
  border-top: 1px solid rgb(40, 44, 52);
  & div {
    padding: 1em;
    max-width: 480px;
    margin: auto;
    color: gray;
  }
}
